<template>
    <div class="user-table-before-detail">
        <el-row style="margin-bottom:10px">
            <el-button type="primary" @click="handleBack">返回</el-button>
            <el-button type="primary" @click="handleRefresh">刷新</el-button>
            <span class="userStyle">用户：{{userName}}</span>
        </el-row>
        <el-table
            :data="tableData"
            :border=true
            tooltip-effect="dark"
            style="width: 100%"
            type="index"
            v-loading="tableLoading"
        >
            <template v-for="(item) in tableHeader">
                <el-table-column v-bind:prop="item.name" v-bind:label="item.label" v-bind:width="item.width" v-bind:type="item.name=='index'?'index':''" :resizable=true sortable>
                </el-table-column>
            </template>
        </el-table>
        <el-pagination
            style="margin-top:10px"
            :total="13"
            :page-sizes="[5,10,15]"
            layout="total, sizes, prev, pager, next, jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="1"
            :page-size="10"
        >
        </el-pagination>
    </div>
</template>
<script>
    export default {
        props:{//获取子组件传递过来的值
            userName:String,
            required:true
        },
        data(){
            return {
                tableHeader:[
                    {label:'#',name:'index',width:'80'},
                    {label:'工资发放月份',name:'month'},
                    {label:'工资所属月份',name:'month2'},
                    {label:'应发工资',name:'pays'},
                    {label:'扣除工资',name:'removePay'},
                    {label:'实发工资',name:'pay'}
                ],
                tableData:[],
                tableLoading:false,
                delLoading:false,
            }
        },
        mounted:function(){
            this.handleRefresh()
        },
        methods:{
            //刷新
            handleRefresh:function(){
                //发起后台请求，将请求的数据加载到表格中
                let self = this;
                this.tableLoading = true;
                setTimeout(function(){
                    self.tableData = [
                        {
                            id:1,
                            month: '2018-01',
                            month2: '2017-12',
                            pays:3000,
                            removePay:200,
                            pay:2800,
                        },
                        {
                            id:2,
                            month: '2018-02',
                            month2: '2018-03',
                            pays:3200,
                            removePay:200,
                            pay:3000,
                        },
                        {
                            id:3,
                            month: '2018-03',
                            month2: '2018-04',
                            pays:3100,
                            removePay:220,
                            pay:2880,
                        },
                        {
                            id:4,
                            month: '2018-04',
                            month2: '2018-05',
                            pays:3500,
                            removePay:200,
                            pay:3300,
                        },
                        {
                            id:5,
                            month: '2018-05',
                            month2: '2018-06',
                            pays:3050,
                            removePay:200,
                            pay:2850,
                        }
                    ]
                    self.tableLoading = false
                },1000) 
            },
            handleBack:function(){
                this.$emit('handleChangeTab','table')
            },
            //分页
            handleSizeChange:function(size){
                console.log('size',size)
            },
            handleCurrentChange:function(current){
                console.log('current',current)
            }
        },
    }
</script>
<style scoped lang="less">
    @import '../userTableBefore.less';
</style>



