<template>
    <el-tabs v-model="activeName" @tab-click="handleTabClick"  style="padding:15px">
        <el-tab-pane label="用户管理" name="table">
            <user-table-before @handleChangeTab="tabChange"></user-table-before>   
        </el-tab-pane>
        <el-tab-pane label="详细信息" name="tableDetail" :disabled=true>
            <user-table-before-detail @handleChangeTab="tabChange" :userName="userName"></user-table-before-detail>   
        </el-tab-pane>
    </el-tabs>
</template>
<script>
import userTableBefore from './component/userTableBefore'
import userTableBeforeDetail from './component/userTableBeforeDetail'
export default {
    components:{
        userTableBefore,
        userTableBeforeDetail
    },
    data(){
        return{
            activeName:'table',
            userName:''
        }
    },
    methods:{
        handleTabClick:function(tab){
            console.log(tab,event)
        },
        tabChange:function(activeName,value){
            this.activeName = activeName
            //将主表传递过来的值传入子表
            this.userName = value
        }
    }
}
</script>




